<template>
    <div>
        <v-toolbar tile>
            <v-btn  class="font-weight-bold" large text @click="taskOrderFunc">
              <span :class="taskOrder ? 'active' : ''">ລໍຖ້າຈ່າຍເງິນ</span>
            </v-btn>
            <v-divider
                    vertical
            ></v-divider>
            <v-btn class="font-weight-bold" large text @click="taskWorkingFunc">
               <span :class="taskWorking ? 'active' : ''">ກໍາລັງເຮັດ</span>
            </v-btn>
            <v-divider
                    vertical
            ></v-divider>
            <v-btn class="font-weight-bold" large text @click="taskPendingFunc">
                <span :class="taskPending ? 'active' : ''">ລໍຖ້າອະນຸມັດ</span>
            </v-btn>
            <v-divider
                    vertical
            ></v-divider>
            <v-btn class="font-weight-bold" large text @click="taskSuccessfulFunc" >
                <span :class="taskSuccessful ? 'active' : ''">ສໍາເລັດ</span>
            </v-btn>
            <v-divider
                    vertical
            ></v-divider>
            <v-btn class="font-weight-bold" large text @click="taskUnsuccessfulFunc" >
                <span :class="taskUnsuccessful ? 'active' : ''">ບໍ່ສໍາເລັດ</span>
            </v-btn>
        </v-toolbar>
        <app-task-order
                        :orders="ordersFilter"
                        :current="taskOrder ? 'ລໍຖ້າຈ່າຍເງິນ' : ( taskWorking ? 'ກໍາລັງເຮັດ' : ( taskPending ?
                        'ລໍຖ້າອະນຸມັດ' : ( taskSuccessful ? 'ສໍາເລັດ' : 'ບໍ່ສໍາເລັດ' )))"
        ></app-task-order>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex'
    import appTaskOrder from '../../components/task/Order'
    export default {
        components: {
            appTaskOrder
        },
        data: () => ({
            orders: [],
            taskOrder: true,
            taskWorking: false,
            taskPending: false,
            taskSuccessful: false,
            taskUnsuccessful: false
        }),
        computed: {
          ...mapGetters([
              'auth'
          ]),
            ordersFilter() {
             return this.orders.filter((order) => {
                if(this.taskOrder) {
                    return order.quotation.workStatus === 'wait for payment'
                } else if (this.taskWorking) {
                    return order.quotation.workStatus === 'in progress'
                } else if (this.taskPending) {
                    return order.quotation.workStatus === 'pending'
                } else if (this.taskSuccessful) {
                    return order.quotation.workStatus === 'successful'
                } else if (this.taskUnsuccessful) {
                    return order.quotation.workStatus === 'unsuccessful'
                }
              })
            }
        },
        methods: {
            async fetchOrders () {
                const results = await this.$axios.get('order/orders', { params: { authId: this.auth._id } })
                this.orders = results.data.orders
            },
            taskOrderFunc () {
                this.taskOrder = true
                this.taskWorking = false
                this.taskPending = false
                this.taskSuccessful = false
                this.taskUnsuccessful = false
            },
            taskWorkingFunc () {
                this.taskWorking = true
                this.taskOrder = false
                this.taskPending = false
                this.taskSuccessful = false
                this.taskUnsuccessful = false
            },
            taskPendingFunc () {
                this.taskPending = true
                this.taskOrder = false
                this.taskWorking = false
                this.taskSuccessful = false
                this.taskUnsuccessful = false
            },
            taskSuccessfulFunc () {
                this.taskSuccessful = true
                this.taskOrder = false
                this.taskWorking = false
                this.taskPending = false
                this.taskUnsuccessful = false
            },
            taskUnsuccessfulFunc () {
                this.taskUnsuccessful = true
                this.taskSuccessful = false
                this.taskOrder = false
                this.taskWorking = false
                this.taskPending = false
            }
        },
        created () {
            this.fetchOrders()
        }
    }
</script>

<style lang="scss" scoped>
    .active {
        color: #3498DB;
        border-bottom: 1px solid #3498DB;
    }
</style>
